import { useState } from 'react';
import { useNavigate } from 'react-router-dom';
import { useDispatch } from 'react-redux';
import { useFormik } from 'formik';
import { forgotPasswordAuth } from '../../../redux/auth/operations';
import schemas from 'utils/schemas';

import { Section, Container } from 'components/baseStyles/CommonStyle.styled';
import { theme } from 'components/baseStyles/Variables.styled.js';
import {
  FormInputLogin,
  FormStyled,
  TitleLogin,
  BoxText,
  StyledLink,
  ErrorBox,
} from '../LoginForm/LoginForm.styled.js';
import { FormLabel, Error, FormField } from 'components/baseStyles/Form.styled.js';
import { BtnLight } from 'components/baseStyles/Button.styled.js';

const ForgotPasswordForm = () => {
  const [isLoading, setIsLoading] = useState(false);
  const dispatch = useDispatch();
  const navigate = useNavigate();

  const formik = useFormik({
    initialValues: { email: '' },
    validationSchema: schemas.changePasswordSchema,
    onSubmit: async (values) => {
      try {
        setIsLoading(true);
        dispatch(forgotPasswordAuth({ email: values.email }));
        navigate('/login');
      } catch (error) {
        console.error('Forgot password error:', error);
      } finally {
        setIsLoading(false);
      }
    },
  });

  const isSubmitDisabled = !!formik.errors.email || !formik.values.email || !formik.touched.email;

  const showAccentValidateInput = (hasValue, hasError) => {
    return !hasValue ? null : hasError ? theme.colors.red : theme.colors.darkGreen;
  };

  return (
    <Section>
      <Container>
        <FormStyled onSubmit={formik.handleSubmit} autoComplete='off'>
          <TitleLogin>Forgot Password</TitleLogin>

          <FormField>
            <FormLabel htmlFor='email'>
              <span>E-mail</span>
              {formik.errors.email && formik.touched.email && <Error>{formik.errors.email}</Error>}
            </FormLabel>

            <FormInputLogin
              style={{
                borderColor: showAccentValidateInput(formik.values.email, formik.errors.email),
              }}
              name='email'
              type='email'
              value={formik.values.email}
              onChange={formik.handleChange}
              onBlur={formik.handleBlur}
            />

            {formik.errors.email && formik.touched.email && (
              <ErrorBox style={{ bottom: '22px' }}>{formik.errors.email}</ErrorBox>
            )}
          </FormField>

          <BtnLight
            type='submit'
            disabled={isSubmitDisabled || isLoading}
            aria-label='submit to change password'
          >
            {isLoading ? 'Loading...' : 'Change'}
          </BtnLight>

          <BoxText>
            <span>Already have an account?</span>
            <StyledLink to='/login'>Log In</StyledLink>
          </BoxText>
        </FormStyled>
      </Container>
    </Section>
  );
};

export default ForgotPasswordForm;
